import React, { useState, useEffect } from "react";
import "./index.css";

const IndexPage = () => {
  // 设置初始主题为 dark 模式
  const [theme, setTheme] = useState("light");

  // 根据主题状态更新 body 的类名
  useEffect(() => {
    document.body.className = theme;
  }, [theme]);

  return (
    <main className="container">
      <section className="left">
        <div className="theme-switch">
          <input
            type="radio"
            id="light"
            name="theme"
            value="light"
            checked={theme === "light"}
            onChange={() => setTheme("light")}
          />
          <label htmlFor="light" className="light">
            <span className="label-text">LIGHT</span>
          </label>
          <input
            type="radio"
            id="dark"
            name="theme"
            value="dark"
            checked={theme === "dark"}
            onChange={() => setTheme("dark")}
          />
          <label htmlFor="dark" className="dark">
            <span className="label-text">DARK</span>
          </label>
        </div>
      </section>

      <section className="right">
        <div className="right-top">
          <div className="name-and-image">
            <div className="name">
              <h1>Shayne Wang</h1>
              <h5>Front-end Engineer</h5>
            </div>
            <div className="image">
              <img src="path_to_image.jpg" alt="Shayne Wang" />
            </div>
          </div>
        </div>
        <div className="divider"></div>
        <div className="right-bottom">
          <nav className="navigation">
            <ul>
              <li>
                <a href="/projects">Projects</a>
              </li>
              <li>
                <a href="/resume" target="_blank">
                  Resume
                </a>
              </li>
              <li>
                <a href="/about-me">About me</a>
              </li>
              <li>
                <a href="/contact">Contact</a>
              </li>
            </ul>
          </nav>
          <div className="introduction">
            <p>Passionate front-end developer eager to fuse IT with</p>
            <p>diverse fields to drive innovation and create</p>
            <p>transformative experiences.</p>
          </div>
        </div>
      </section>
    </main>
  );
};

export const Head = () => <title>Home Page</title>;

export default IndexPage;
